@import '../../../Variables/variables';

.calendar {
  width: 364px;
  height: 410px;
  margin: 0 auto;

  @media screen and (min-width: $mb) {
    width: 1024px;
    height: 540px;
  }

  .select_month {
    display: flex;
    justify-content: space-evenly;
    margin: 0 auto;
    font-size: 12px;
    font-weight: normal;
    margin-bottom: 28px;

    .selectbutton_month{
      cursor: pointer !important;
      &:hover {
        opacity: 0.7;
      }
    }
    

    @media screen and (min-width: $mb) {
      font-size: 16px;
    }
  }

  
  .calendar_body {
    width: 100%;
    height: 368px;
    border-collapse: collapse;
    text-align: center;

    @media screen and (min-width: $mb) {
      height: 516px;
    }

    .calendar_day {
      th {
        font-size: 16px;
        font-weight: normal;
  
        &:first-child {
          color: $amount_red;
        }
  
        &:last-child {
          color: $amount_blue;
        }
      }
    }
    
    .calendar_main {

      .calendar_week {
        
        border-bottom: 0.5px solid;
  
        .calendar_date {
          width: 38px;
          height: 52px;
          padding-top: 0;
  
          .date {
            font-size: 24px;
            font-weight: normal;
            margin-bottom: 8px;

            @media screen and (min-width: $mb) {
              margin-bottom: 16px;
            }
          }
  
          .sunday{
            color: $amount_red;
          }
        
          .saturday{
            color: $amount_blue;
          }
  
          .date_amount {
            height: 16px;
            overflow-y: scroll;
            overflow-x: hidden;
            color: $amount_red;
            font-size: 8px;
            font-weight: normal;
            margin-bottom: 0;
  
            // バー非表示
            -ms-overflow-style: none;
            scrollbar-width: none;
            &::-webkit-scrollbar {
              display:none;
            }

            @media screen and (min-width: $mb) {
              height: 24px;
              font-size: 16px;
            }
          }
        }
      }
    }
    
    
    
  }



}